$color1:#dc5ca9;

.box_dichvu {
	.art_item {
		margin-bottom: 15px;
	}
	.sub_item {
		padding: 0 14px;
		.row {
			display: -webkit-flex;
			display: -ms-flexbox;
			display: flex;
			-webkit-flex-wrap: wrap;
			-ms-flex-wrap: wrap;
			flex-wrap: wrap;
		}
		.item {
			width: 50%;
			float: left;
			padding: 0 14px;
			margin-bottom: 14px;
		}
		.title_news {
			margin-top: 12px;
			font: 700 px_to_rem(18px) $fontmain; line-height: $lh;
		}
	}
	.featured_item {
		padding: 0 14px; 
		.title_news {
			margin-top: 12px;
			font: 700 px_to_rem(28px) $fontmain; line-height: $lh;
		}
	}
}

.box_service {
	.item {
		float: left;
		margin-bottom: 20px;
		a {
			display: table;
		}
		&:hover, &.active {
			.title {
				font-weight: 700;
				color: $color1;
			    position: relative;
    			left: -1px;
			}
		}
	}
	.icon_thumb {
		display: inline-block;
		width: 32px;
		svg {
			color: $color1;
		}
	}
	.title {
	    width: calc(100% - 32px);
	    padding-left: 10px;
	    font-weight: 400;
	    display: table-cell;
	    vertical-align: middle;
	    white-space: nowrap;
	    font-size: px_to_rem(16px);
	}
	.intro_text {
		font-size: 16px; line-height: 16px*1.3;
		width: calc(100% - 160px); padding-right: 28px;
		a {
			color:$color1; font-weight: 700;
		}
	}
	.btn {
		width: 160px; color: #fff;
		background: $color1; padding-top: 8px; padding-bottom: 8px; border-radius: 4px;
	}
}

.box_slide_service {
	&:not(:last-of-type) {
	    padding-bottom: 20px;
	    margin-bottom: 20px;
	    border-bottom: 1px solid #eee;
	}
