		font: 700 px_to_rem(20px) $fontmain; margin-bottom: 10px; line-height: $lh;
	}
	.owl-stage {
		padding-left: 0 !important;
	}
	.item {
		max-width: 196px;
		h3 {
			font: 400 px_to_rem(16px) $fontmain; line-height: $lh; margin-top: 10px;
		}
	}
	.owl-carousel .owl-item {
		
	}
	.owl-nav{
      &>div{
         position: absolute;
	    top: 20px;
	    padding: 26.5px 7px;
	    background: rgba(255,255,255,0.7);     
         .icon-ns{
            color: #fff;
            margin: 0;
         }
      }
      .owl-prev{left: 0;}
      .owl-next{right: 0}
   }
}

.box_filter {
	position: relative; font-size: px_to_rem(16px);
}
.boloc {
	position: absolute; right: 0; top: 0;
	a { font-weight: 700;
		svg {
			color: #333;
		}
	}
}
#show_timkiemdv {
	display: none;
	&.show {
		display: block;
	}
}

.dvc_detail#breadcrumb ul li:first-of-type h2 {
    font: 500 15px/1.313 iCielDomaineText;
}
/*TAB*/

.tab-nav {
    float: left; width: 100%; border-bottom: 1px solid #eee;
    margin-bottom: 10px;
    li {
        position: relative;
        display: inline-block;
        margin-right: 14px;
        a {
        	display: block;
        	font-size: 16px; 
        	font-weight: 700;
        	padding-bottom: 5px;
        }
       
        &.active {
        	&:after {
                content: "";
                width: 100%; height: 1px;
                background: $color1;
                position: absolute;
                right: 0; left: 0;
                bottom: -1px;
            }
            a {
                color: $color1;

            }
        }
    }
}

.tab-content {
	float: left; width: 100%;
	.tab-item {
		display: none;
		&:after {
			content: "";
			clear: both;
			display: table;
		}
		&.active {
			display: block;
		}
	}
}

/*End Tab*/


.box_filter {
