		color: #666666;
		background: #e5e5e5;
		border-radius: 4px;
	}
	
}


.box_result {
	.btn {
		padding: 7px 25px;
		min-width: 160px;
		color: #fff;
		background: $color1;
		border-radius: 4px;
	}
	.item {
		float: left; width: 100%;
		padding-bottom: 15px; margin-bottom: 15px;
		border-bottom: 1px solid #eee;
		.thumb_art {
			width: 196px; float: left; margin-right: 25px;
		}
		.info {
			margin-top: 20px;
			.title {
				font: 700 px_to_rem(18px) $fontmain; margin-bottom: 5px;line-height: 1.3;
			}
