}
form {
        .form-group {
            position: relative;
            float: left;
            width: 100%;
            &_title {
                padding: 8px 0;
			    float: left;
			    width: 100%;
                h3 {
                   font-size: 16px;
                }
            }
            .checkbox {
                width: 100%;
                margin-top: 8px;
                padding-left: 25px;
                position: relative;
                border-bottom: 1px solid #eee;
                clear: both;
                label {
                    display: inline-block;
                    padding-left: 0px;
                    cursor: pointer;
                    span {
                        position: relative;
                        float: left;
                        
                    }
                    svg {
                    	display: none;
                    }
                    input {
                        top: 0px;
                        width: 15px;
                        height: 15px;
                    }
                    input[type="checkbox"] {
                        display: none;
                    }
                    span:before {
                        content: "";
                        position: absolute;
                        width: 9px;
                        height: 9px;
                        top: 5px;
                        left: -25px;
                        border: 1px solid #ccc;
                        border-radius: 2px;
                    }

                    input[type="checkbox"]:checked + svg {
            	        display: inline-block;
					    position: absolute;
					    top: 6px;
					    left: 2px;
					    color: #dc5ca9;
					    width: 8px;
					    height: 8px;
					    text-align: center;
                    }
                }
            }

        }

    }
