	font-size: px_to_rem(17px);
	.form-group .checkbox {
		border: none;
		label span:before {
		    width: 15px;
		    height: 15px;
		    top: 1px;
		    left: -25px;
		}
		label input[type="checkbox"]:checked + svg {
		    top: 3px;
		    left: -1px;
		    width: 12px;
		    height: 12px;
		}
	}

}

.files-list {
	display: -webkit-flex-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
	margin-right: -10px;
	margin-left: -10px;
	margin-top: 8px;
	.item {
		position: relative;
		float: left;
		position: relative;
		width: 100%;
		min-height: 1px;
		padding-right: 10px;
		padding-left: 10px;
		width: 20%;
		box-sizing: border-box;
		margin-bottom: 40px;
		svg {
	        position: absolute;
		    bottom: 4px;
		    color: #dc5ca9;
		    left: 12px;
		}
		> .form-group {
			position: absolute;
			left: 5px; right: 0;
			bottom: -5px;
			label {
				color: $color1; position: relative;
				span {
					position: relative;
					float: left;
					padding-left: 26px;                            
				}

				input {
					display: none;
					margin-right: 4px;
					margin-top: 1px;
					float: left;
				}  
				input[type="radio"] + span:before {
					content: "";
					position: absolute;
					width: 16px;
					height: 16px;
					top: 0px;
					left: 0px;
					border: 1px solid #ccc;
					border-radius: 2px;
				}
				input[type="radio"]:checked + span:after {
					font: normal normal normal 12px/1 FontAwesome;
					content: "\f00c";
					position: absolute;
					top: 0px;
					left: 0px;
					background: $color1;
					color: #fff;
					width: 16px;
					height: 16px;
					text-align: center;
					line-height: 16px;
					border-radius: 2px;
				}
			}

		}
		> a {
			    position: absolute;
			    bottom: -28px;
			    color: #999999;
			    margin: auto;
			    left: 0;
			    right: 0;
			    text-align: center;
			
		}
		&_img {
			img {
				display: block;
				width: 100%;
			}
		}
		&-inner {
			width: 100%;
			height: 100%;
		}
		&:hover {
			> a {
				display: block;
			}
		}
	}
	.file-upload {
		.item-inner {
			position: relative;
			display: flex;
			display: -webkit-flex-box;
			display: -ms-flexbox;
			display: -moz-flexbox;
			justify-content: center;
			align-items: center;
			background: #f0f0f0;
			padding: 10px;
			box-sizing: border-box;
			text-align: center;
			i {
				font-size: 24px;
			}
			span {
				display: block;
			}
			em {
				font-size: 12px;
			}
			svg {
			    color: #999999;
			    width: 28px;
			    height: 28px;
			    left: 0;
			    right: 0;
			    margin: auto;
			    top: 0;
			    bottom: 0;
			}
		}

		input[type='file'] {
			
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			opacity: 0;
			cursor: pointer;
		}
	}
}
